.services {
	background-color: $dark;

	&-subtitle {
		font-family: $openSans;
		font-weight: $fw400;
		font-size: $fs16px;
		line-height: $lh32px;
		color: $light-grey;
		text-align: center;
		padding: 40px 0 12px 0;

		@media (min-width: $md) {
			font-size: $fs20px;
			padding: 40px 0 12px 0;
		}

		@media (min-width: $lg) {
			padding: 80px 0 12px 0;
			text-align: center;
		}
	}

	&-title {
		font-family: $manrope;
		font-weight: $fw400;
		font-size: $fs32px;
		line-height: $lh72px;
		text-align: center;
		color: $white;
		margin-bottom: 65px;

		@media (min-width: $md) {
			font-size: $fs36px;
		}

		@media (min-width: $lg) {
			font-size: $fs48px;
			text-align: center;
		}
	}
}

.services-cards {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -20px;

	@media (min-width: $xl) {
		margin: 0 -50px;
	}
}
	.card {
		width: calc(100% / 1);
		padding: 0 20px;

		@media (min-width: $md) {
			width: calc(100% / 2);
			flex-grow: 1;
		}

		@media (min-width: $xl) {
			width: calc(100% / 3);
			padding: 0 50px;
		}
		
		&-inner {
			display: flex;
			align-items: center;
			flex-direction: column;
			margin-bottom: 60px;

			@media (min-width: $xl) {
				margin-bottom: 96px;
				align-items: stretch;
			}
		}
		
		&-image {
			max-width: 300px;
			
			& img {
				max-width: 300px;
				max-height: 354px;
			}
		}

		&-text {
			font-family: $openSans;
			font-weight: $fw400;
			font-size: $fs24px;
			line-height: $lh36px;
			color: $white;
			margin: 24px 0 28px 0;
		}

		&-link {
			font-family: $openSans;
			font-weight: $fw400;
			font-size: $fs20px;
			line-height: $lh36px;

			& a {
				color: $white;
				transition: color ease 0.35s;
				vertical-align: middle;
			}

			& a:hover {
				content: "";
				color: $accent;
				cursor: pointer;
			}

			&.img {
				max-width: 24px;
				max-height: 24px;
			
			}

			& img {
				max-width: 100%;
				vertical-align: middle;
				margin-left: 12px;
			}
		
			&-link:after {
				content: "";
				display: block;
				border-bottom: solid 1px;
				color: $white;
				width: 65%;
			}
		}
	}